.TodoSearch {
  @apply
  bg-[#F9FBFC] border-[2px] border-[#202329]
  m-0 ml-6 h-16 w-11/12 text-2xl text-center font-Montserrat font-normal text-[#1E1E1F]
  shadow-InputSearch

  focus:outline-none focus:border-[#61DAFA]

  placeholder:text-[#A5A5A5] placeholder:font-Montserrat placeholder:font-normal
}

