.LoadingTodo-container{
    @apply
    relative flex justify-center items-center mt-6 shadow-TodoItem
}

.LoadingTodo-text{
    @apply
    mt-6 mr-0 mb-6 ml-6 w-11/12 text-lg leading-6 font-normal 
}

.LoadingTodo-completeIcon, 
.LoadingTodo-deleteIcon{
    @apply
    rounded-[50px] h-12 w-12
}

.LoadingTodo-completeIcon{
    @apply
    absolute left-3
}

.LoadingTodo-deleteIcon{
    @apply
    absolute top-[-24px] right-0
}
.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    @apply
    bg-gradient-to-r from-slate-100 to-slate-300 animate-bounce
}

